@keyframes startupBackground {
  from {
    transform: scale(1.1, 1.1);
    opacity: 0; }
  to {
    transform: scale(1, 1);
    opacity: 1; } }

@keyframes finishBackground {
  from {
    transform: scale(1, 1);
    opacity: 1; }
  to {
    transform: scale(1.1, 1.1);
    opacity: 0; } }

@keyframes startupContent {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes finishContent {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

html.modal-viewing,
body.modal-viewing {
  overflow: hidden; }

.modales-scene > :first-child {
  transition: filter 0.2s; }

.modales-scene.blurred > :first-child {
  filter: blur(5px); }

.modal-viewer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  transition: filter 0.2s; }
  .modal-viewer.blurified {
    filter: blur(5px); }
  .modal-viewer.blurred > .background {
    background-color: rgba(0, 0, 0, 0.5); }
  .modal-viewer.translucent > .background {
    background-color: rgba(0, 0, 0, 0.8); }
  .modal-viewer.static-init > .background {
    opacity: 1; }
    .modal-viewer.static-init > .background > .wrapper > .content {
      transform: scale(1, 1);
      opacity: 1; }
  .modal-viewer:not(.static-init) > .background {
    animation-name: startupBackground;
    animation-duration: 0.2s; }
    .modal-viewer:not(.static-init) > .background > .wrapper > .content {
      animation-name: startupContent;
      animation-duration: 0.2s; }
  .modal-viewer > .background {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: scroll;
    -webkit-overflow-scrolling: touch; }
    .modal-viewer > .background > .wrapper {
      display: flex;
      align-items: center;
      min-height: 100vh;
      height: max-content; }
      .modal-viewer > .background > .wrapper > .content {
        height: max-content; }
  .modal-viewer.closed > .background {
    animation-name: finishBackground;
    animation-duration: 0.2s;
    animation-fill-mode: forwards; }
    .modal-viewer.closed > .background > .wrapper > .content {
      animation-name: finishContent;
      animation-duration: 0.2s;
      animation-fill-mode: forwards; }
